html, body {
    width: 100%;
    height: 100%;
    background: #E0F2F1;
    background: -webkit-gradient(linear, left top, left bottom, from(#E0F2F1), to(#CFD8DC));
    background: -webkit-linear-gradient(#E0F2F1, #CFD8DC);
    background: -o-linear-gradient(#E0F2F1, #CFD8DC);
    background: linear-gradient(#E0F2F1, #CFD8DC);
}
.badge {
    margin-left: 7px;
}
.fade-in {
    -webkit-animation: fadein 2s;
    animation: fadein 2s;
}
.loading-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 90vh;
}
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}



